.contaner {
    width: 500px;
    height: 350px;
}
.message {
    display: grid;
    grid-template-areas:
    "ava messageArea";
    grid-template-columns: 80px 220px;
    grid-gap: 10px;
    width: 500px;
    height: 350px;
    background: palevioletred ;

    margin: auto;
}
.message img {
    margin: 10px;
    width: 80px;
    height: 80px;
    border-radius: 60px;
}
.ava {
    grid-area: ava;
}
.messageArea {
    margin: 10px;
    grid-area: messageArea;
    background: pink;
    padding: 10px;
    padding: 20px;
    text-align: left;
    width: 300px;
    height: 250px;
    border-radius: 15px;
    border: 3px solid pink;
    display: inline-block;
    position: relative;
}
.messageArea::after {
    content: '';
    width: 39px;
    height: 50px;
    border-left: 3px solid pink;
    border-bottom: 5px solid pink;
    position: absolute;
    bottom: -31px;
    left: 50px;
    background: pink;
    transform: skewX(-30deg) skewY(-52deg);
}

.messageArea span{
  color: lightslategrey;
    font-size: larger  ;
    font-weight: bold;
}
.time {
    text-align: right;
    font-size: small;
    color: lightslategrey;
}